<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="活动名称" label-width="120px">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="活动期限" label-width="120px">
          <div class="block">
            <el-date-picker
              v-model="value1"
              type="datetimerange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </div>
        </el-form-item>

        <el-form-item label="一级分类" label-width="120px">
          <el-select v-model="form.first_cateid" @change="changeFirst">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in classifyList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="二级分类" label-width="120px">
          <el-select v-model="form.second_cateid" @change="changeSecond">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in secondClassifyList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="商品" label-width="120px">
          <el-select v-model="form.goodsid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in goodsList"
              :key="item.id"
              :label="item.goodsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='添加秒杀'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { alertSuccess, alertWarning } from "../../../util/alert";
import { mapActions, mapGetters } from "vuex";
import {
  reqClassifyList,
  reqGoodsList,
  reqSeckAdd,
  reqSeckListOne,
  reqSeckUpdate
} from "../../../util/request";
export default {
  props: ["info"],
  computed: {
    ...mapGetters({
      list: "loop/loop",
      classifyList: "classify/classify"
    })
  },
  components: {},
  data() {
    return {
      form: {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1
      },
      value1: "",
      value2: "",
      secondClassifyList: "",
      goodsList: ""
    };
  },
  methods: {
    //验证
    checked() {
      return new Promise((resolve, reject) => {
        //验证数据是否均不为空
        if (this.form.title === "") {
          alertWarning("活动名称不能为空");
          return;
        }
        if (this.value1.length === 0) {
          alertWarning("活动期限不能为空");
          return;
        }
        if (this.form.first_cateid === "") {
          alertWarning("一级分类不能为空");
          return;
        }
        if (this.form.second_cateid === "") {
          alertWarning("二级分类不能为空");
          return;
        }
        if (this.form.goodsid === "") {
          alertWarning("商品不能为空");
          return;
        }
        resolve();
      });
    },
    changeFirst() {
      this.form.second_cateid = "";
      this.getSecondIdList();
    },
    getSecondIdList() {
      reqClassifyList({ pid: this.form.first_cateid }).then(res => {
        this.secondClassifyList = res.data.list;
      });
    },
    changeSecond() {
      this.form.goodsid = "";
      this.getGoodsIdList();
    },
    getGoodsIdList() {
      let sid = this.form.second_cateid;
      reqGoodsList(sid).then(res => {
        this.goodsList = res.data.list;
      });
    },
    changeFile(e) {
      let file = e.target.files[0];
      if (file.sizw > 2 * 1024 * 1024) {
        alertWarning("文件不能超过2M");
        return;
      }
      let imgArr = [".jpg", ".png", ".gif", ".jpeg"];

      if (!imgArr.includes(file.name.slice(file.name.lastIndexOf(".")))) {
        alertWarning("请上传正确的图片");
        return;
      }
      this.imgUrl = URL.createObjectURL(file);
      this.form.img = file;
    },
    ...mapActions({
      requsetSeckList: "seck/requsetSeckList",
      requsetClassifyList: "classify/requsetClassifyList"
    }),

    empty() {
      this.form = {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1
      };
      (this.value1 = ""),
        (this.value2 = ""),
        (this.secondClassifyList = ""),
        (this.goodsList = "");
    },
    update() {
      var time1 = this.value1[0].getTime();
      this.form.begintime = time1;
      var time2 = this.value1[1].getTime();
      this.form.endtime = time2;
      reqSeckUpdate(this.form).then(res => {
        if (res.data.code === 200) {
          alertSuccess(res.data.msg);
          this.empty();
          this.cancel();
          this.requsetSeckList();
        }
      });
    },
    add() {
      this.checked().then(() => {
        var time1 = this.value1[0].getTime();
        this.form.begintime = time1;
        var time2 = this.value1[1].getTime();
        this.form.endtime = time2;
        reqSeckAdd(this.form).then(res => {
          if (res.data.code === 200) {
            alertSuccess(res.data.msg);
            this.empty();
            this.cancel();
            this.requsetSeckList();
          }
        });
      });
    },
    cancel() {
      this.info.isShow = false;
    },
    getOne(id) {
      reqSeckListOne(id).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.list;
          this.form.id = id;
          var data1 = new Date(res.data.list.begintime * 1);
          var data2 = new Date(res.data.list.endtime * 1);
          this.value1 = [data1, data2];
          this.getGoodsIdList();
          this.getSecondIdList();
        }
      });
    }
  },
  mounted() {
    this.requsetClassifyList();
  }
};
</script>
<style scoped>
.line {
  display: flex;
}
.ipt-box {
  flex: 1;
}
.my-upload {
  width: 100px;
  height: 100px;
  border: 1px dashed #ccc;

  position: relative;
}
.my-upload h3 {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 100px;
  color: #666;
}
.my-upload .ipt {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 2;
  cursor: pointer;
}
.my-upload img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
</style>


